.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 14%;
}

.about__me {
    width: 100%;
    height: 20rem; /* Adjust this value to your desired height */
    aspect-ratio: 2rem;
    border-radius: 2rem;
    background: linear-gradient(45deg, 
                transparent, var(--color-primary), 
                transparent);
    display: grid;
    place-items: center;       
}

.about__me-image {
    border-radius: 2rem;
    height: 100%; /* Match the height of .about__me */
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__content p {
    margin: 2rem 0 1.5rem;
    color: var(--color-light);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    width: 100%;
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 0.8rem 0.5rem 0.8rem 0.5rem;
    text-align: center;
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.8rem;
    color: var(--color-light);
    width: 1rem;
}



/* ================ Media Queries (Medium Devices ) =============== */
@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* ================ Media Queries (Small Devices ) =============== */
@media screen and (max-width: 600px){
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }

}